import React, { useState } from "react";
import api from "./services/api";
import logooggi from "./assets/oggi.png";

import "./App.css";

function App() {
  const InitialForm = {
    name: "",
    email: "",
    phone: "",
  };
  const [form, setForm] = useState(InitialForm);
  const [errors, setErrors] = useState({});

  function handleChange(e) {
    setForm({
      ...form,
      [e.target.name]: e.target.value,
    });
  }

  function validate() {
    let errors = {};

    if (!form.name) {
      errors.name = "Informe o seu nome";
    }
    if (!form.email) {
      errors.email = "Informe um email v√°lido";
    }
    if (!form.phone) {
      errors.phone = "Informe o telefone 11999999999";
    }
    return errors;
  }

  async function handleSubmit(e) {
    e.preventDefault();
    setErrors(validate(form));

    const retorno = await api.post("/leads/3", form);

    if (retorno) {
      alert("Dados Cadastrados. Obrigado!");
    } else {
      alert("Ops. Tente novamente.");
    }

    clearState();
  }

  function clearState() {
    setForm(InitialForm);
  }

  return (
    <>
      <section className="container logo">
        <img src={logooggi} alt="" />
      </section>

      <section className="formulario">
        <div className="content">
          <div className="container">
            <h1>Fique por dentro</h1>
            <p>Preencha os campos abaixo para receber nossos comunicados</p>
            <form onSubmit={(e) => handleSubmit(e)}>
              <div>
                <input
                  type="text"
                  name="name"
                  value={form.name}
                  onChange={(e) => handleChange(e)}
                  placeholder="Informe seu Nome"
                />
                {errors.name && <p className="error-input">{errors.name}</p>}
              </div>
              <div>
                <input
                  type="email"
                  name="email"
                  placeholder="Informe seu email"
                  onChange={(e) => handleChange(e)}
                  value={form.email}
                />
                {errors.email && <p className="error-input">{errors.email}</p>}
              </div>
              <div>
                <input
                  type="number"
                  name="phone"
                  placeholder="Informe seu telefone"
                  pattern="\d{2}\d{5}\d{4}"
                  size="11"
                  onChange={(e) => handleChange(e)}
                  value={form.phone}
                />
                {errors.phone && <p className="error-input">{errors.phone}</p>}
              </div>

              <div className="btn-container">
                <button type="submit" className="btn-cadastrar">
                  Cadastrar
                </button>
              </div>
            </form>
          </div>
        </div>
      </section>
    </>
  );
}

export default App;
